.body__text{
    font-size: 36px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    padding-left: 15px;

    border-radius: 14px;
background: #ededed;
box-shadow:  7px 7px 14px #c9c9c9, 
             -7px -7px 14px #ffffff;
}

.body__text:hover{
    border-radius: 14px;
background: #ededed;
box-shadow: inset 7px 7px 14px #c9c9c9, 
            inset -7px -7px 14px #ffffff;
}

@media only screen and (max-width: 600px) {
    p.body__text{
       margin:0 auto;
    }
}

.body__left{
    /* background-color: brown; */
    margin-top: 15vh;
    margin-bottom: 10px;
}


@media only screen and (max-width: 600px) {
    div.body__left{
       margin-top: 4vh;
    }
}



.body__right{
    text-align: center;
    /* background-color: blueviolet; */
    margin-top: 15vh;
    margin-bottom: 10px;
}

@media only screen and (max-width: 600px) {
    div.body__right{
       margin-top: 2px;
    
    }
  
}

.body__rightImage{
   
    /* height: 50%; */
    height:210px;
    
}

@media only screen and (max-width: 600px) {
    img.body__rightImage{
        height: 160px;
        margin-top: 20px;
    }
  
}

.body__cat{
    /* background-color: darkgreen; */
    margin-top: 40px;
    padding-bottom:70px;
    
}

@media only screen and (max-width: 600px) {
    div.body__cat{
        margin-top: 20px;
        padding-bottom: 40px;
    }
   
}

.body__button{
    text-align: center;

    
  
    display: block;
    width: 100%;

    font-size: 25px;

    font-weight: 700;
    font-family: 'Montserrat', sans-serif;

}

.body__arrow{
    height:30px;
    margin-left: 30px;
}

.cat__neuro{
    padding-left: 15px;
    border-radius: 14px;
background: #ededed;
box-shadow:  7px 7px 14px #c9c9c9, 
             -7px -7px 14px #ffffff;

       

}

.cat__neuro:hover{
    border-radius: 14px;
background: #ededed;
box-shadow: inset 7px 7px 14px #c9c9c9, 
            inset -7px -7px 14px #ffffff;
}


.big__letter{
    font-size: 56px;
}

@media (max-width:575.98px){
    h2.cat__neuro{
        margin-bottom:2.5rem;
    }
    p.body__text{
        font-size: 26px;
    }
}


