.about{
    background-color: #fff;
    text-align: center;
}
@media only screen and (max-width: 600px) {
    div.about{
        overflow-x: hidden;
        overflow-y: hidden;
    }
  }

.about__imgDiv{
    text-align: center;
}

.about__title{
    color: #000;
    padding:30px;
}

.about__img{
    height: 150px;
}

.about__text{
    text-align: center;
    margin-top:30px;
    margin-bottom:40px;
    /* background-color: brown; */
    
}

.about__text p{
    align-self: center;
    max-width:700px;
    margin: auto;
}

.about__neuro{
   margin-top: 30px;
   padding-bottom: 10px;
   margin-bottom: 30px;
    border-radius: 10px;
background: linear-gradient(145deg, #ffffff, #e6e6e6);
box-shadow:  6px 6px 14px #666666, 
             -8px -8px 18px #ffffff;

         
}

.about__neuro:hover{
    border-radius: 10px;
background: #ffffff;
box-shadow: inset 6px 6px 17px #666666, 
            inset -6px -6px 17px #ffffff;
}