.contact{
    background-color: black;
    padding:20px 0;
}


.contact__header{
    display: flex;
    color: #fff;
    justify-content: space-between;
    
}

@media only screen and (max-width: 600px) {
    div.contact__header{
        flex-direction: column;
    }
}


.contact__body{
    color:#fff;
    
    align-content: center;
    text-align: center;
    /* background-color: brown; */
}

.contact__describe textarea{
    display: block;
    width: 100%;
}


.contact__button{
    width: 100%;
    margin:20px 0;
}

.contact__footer{
    color: lemonchiffon;
    text-align: center;
}

.contact__footerLogo{
    text-align: center;
    margin:1rem 0;
}

.footer__link{
    text-decoration: none;
    color: gray;
    margin:0 20px;
    font-size: 2rem;
}

.footer__link:hover, .copyright:hover{
    color: #fff;
}

.copyright{
    color:gray;
    padding-bottom:1rem;
}

.contact__iframe{
    background-color: black;
    width: 100%;
  
}